export default "import { BehaviorSubject, Observable } from 'rxjs';\n\ninterface Params {\n  blob: Blob;\n  signal?: AbortSignal;\n}\n\nexport function fileToBuffer({ blob, signal }: Params): Observable<number | ArrayBufferLike> {\n  const subject = new BehaviorSubject<number | ArrayBufferLike>(0);\n\n  const reader = new FileReader();\n\n  const abort = () => {\n    try {\n      reader.abort();\n    } catch (error) {}\n\n    signal?.removeEventListener('abort', abort);\n  };\n\n  signal?.addEventListener('abort', abort);\n\n  reader.onloadstart = () => {\n    subject.next(0);\n  };\n\n  reader.onloadend = () => {\n    subject.next(reader.result as ArrayBufferLike);\n\n    signal?.removeEventListener('abort', abort);\n  };\n\n  reader.onprogress = ({ loaded, lengthComputable, total }: ProgressEvent) => {\n    if (lengthComputable) {\n      subject.next(loaded / total);\n    }\n  };\n\n  reader.readAsArrayBuffer(blob);\n\n  return subject.asObservable();\n}\n"