export default "import { BehaviorSubject, Observable } from 'rxjs';\n\ninterface Params {\n  url: string;\n  signal?: AbortSignal;\n  headers?: Headers | [string, string][] | { [key: string]: string };\n}\n\nexport function fetchBuffer({ url, signal, headers }: Params): Observable<number | ArrayBuffer> {\n  const subject = new BehaviorSubject<number | ArrayBuffer>(0);\n\n  const xhr: XMLHttpRequest = new XMLHttpRequest();\n\n  xhr.open('get', url, true);\n  xhr.responseType = 'arraybuffer';\n\n  if (headers instanceof Headers || Array.isArray(headers)) {\n    for (const [k, v] of headers) {\n      xhr.setRequestHeader(k, v);\n    }\n  } else if (headers) {\n    Object.keys(headers).forEach(key => {\n      xhr.setRequestHeader(key, headers[key]);\n    });\n  }\n\n  const abort = () => {\n    try {\n      xhr.abort();\n    } catch (error) {}\n\n    signal?.removeEventListener('abort', abort);\n  };\n\n  signal?.addEventListener('abort', abort);\n\n  xhr.onloadstart = () => {\n    subject.next(0);\n  };\n\n  xhr.onreadystatechange = () => {\n    if (xhr.readyState === 4 && xhr.status === 200) {\n      subject.next(xhr.response);\n\n      signal?.removeEventListener('abort', abort);\n    }\n  };\n\n  xhr.onprogress = ({ loaded, lengthComputable, total }: ProgressEvent) => {\n    if (lengthComputable) {\n      subject.next(loaded / total);\n    }\n  };\n\n  xhr.send();\n\n  return subject.asObservable();\n}\n"